const state = () => ({
    isInit: false, // 
    graph: null,
    cellId: null, // id
    selectedCell: null, // ,
    configType: 0,
    contextmenuNode: null // 
});
const mutations = {
    setIsInit(state, v) {
        state.isInit = v
    },
    setGraph(state, v) {
        state.graph = v;
    },
    setCellId(state, v) {
        state.cellId = v;
    },
    setSelectedCell(state, v) {
        state.selectedCell = v;
    },
    setConfigType(state, v) {
        state.configType = v;
    },
    setContextmenuNode(state, v) {
        state.contextmenuNode = v;
    }
};
const actions = {};

export default {
    namespaced: true,
    state,
    mutations,
    actions
};

